body {
  margin: 0;
}
#app {
  width: 50vw;
}

.container {
  width: 100vw;
  display: flex;
  justify-content: center;
  background-color: aliceblue;
}

header {
  display: flex;
  justify-content: space-evenly;
  position: sticky;
  margin-left: -25vw;
  top: 0px;

  background-color: aliceblue;
  width: 100vw;
}

h1 span {
  font-weight: normal;
  font-size: small;
}

h1 {
  margin-left: 10px;
  margin-bottom: 0px;
}

[name="countrySearch"] {
  height: 20px;
  margin: 30px;
  margin-right: 10px;
  margin-bottom: 0px;
}

.flag {
  width: 5vw;
  height: 5vw;
  border-radius: 50%;
  background-size: cover;
  background-position: center;
  margin-right: 10px;
  border: 1px solid black;
  margin: 7.5px;
}

/* #countryList {
  background-color: blanchedalmond;
} */

.country {
  display: flex;
  width: 95%;
  min-width: 60vw;
  border: 1px solid black;
  margin: 20px;
  background-color: burlywood;
}
.country:hover {
  background-color: khaki;
  cursor: pointer;
}

.continent {
  color: darkslategrey;
}

.background,
.background2 {
  background-color: rgba(72, 72, 66, 0.6);
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.background2 {
  display: none;
}
.popUp{
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 30rem;
  max-height: 80vh;
  width: 25rem;
  max-width: 80vw;
  background-color: tan;
  border-radius: 10%;
  margin: 0px;
}

#big {
  border-radius: 5px;
  width: 20rem;
  max-width: 70vw;
  height: 10rem;
}

h3 {
  font-weight: bold;
  font-size: x-large;
  margin: 12px;
}

.popUp #big {
  margin: 10px;
  margin-top: 0;
}
.info{
  margin: 5px;
  margin-left: 2px;
}

.info h4 {
  margin-bottom: 10px;
  margin: 7.5px;
  height: max-content;
  width: max-content;
  padding: 0;
  font-size: 20px;
  display: inline-block;
}

.info span,
.info span {
  font-weight: normal;
  font-size:large;
  color: black;
}

.flex {
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  flex-wrap: wrap;
  font-size: larger;
}

.flex-center {
  display: flex;
  justify-content: center;
}

.flex-center ul {
  display: flex;
  flex-direction: column;
  justify-content:end ;
  margin: 0;
}
